@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap');
body {
    margin: 0;
    font-family: roboto;
}

.all {
    background-color: hsl(0, 0%, 100%);
}

header {
    display: flex;
    position: fixed;
    width: 100%;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.2);
    background-color: hsl(0, 0%, 100%);
    color: rgb(18, 18, 18);
}

header > h3 {
    width: 47%;
    text-align: right;
}


header > button {
    height: 40px;
    margin: 10px 0 10px 10px;
    padding: 0 10px 0 10px;
    width: 70px;
    border: none;
    background-color: hsl(234, 92%, 63%);
    color: white;
    border-radius: 10px;
    cursor: pointer;
    font-size: 12px;
    transition: all 0.3s;
}

header > button:hover {
    background-color: hsl(234, 100%, 74%);
}
.messege {
    background-color: hsl(0, 0%, 74%);
    max-width: 300px;
    min-width: 150px;
    text-align: center;
    border-radius: 0px 10px 10px 10px;
    color: rgb(26, 26, 26);
    margin: 10px;
}

.myMessege {
    background-color: hsl(234, 92%, 63%);
    max-width: 300px;
    min-width: 150px;
    text-align: center;
    border-radius: 10px 0px 10px 10px;
    color: white;
    margin: 10px;
}

.left {
    display: flex;
    justify-content: left;
}
.right {
    display: flex;
    justify-content: right;
}

.name {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 5px 5px 10px 10px;
}

.text {
    padding: 10px;
    margin: 0;
    display: inline-block; 
    overflow-wrap: break-word; 
    max-width: 280px;
}

.type-form {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.type-form > input {
    width: 80%;
    padding-left: 20px;
    border: none;
    height: 30px;
    border-radius: 10px;
    background-color: hsl(0, 0%, 74%);
}

.auth-form {
    display: flex;
    flex-direction: column;
    width: 300px;
}
.auth-form > input {
    margin-bottom: 10px;
    height: 30px;
    border: none;
    border-radius: 3px;
    padding-left: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.2);
}
.auth-form > button {
    margin-bottom: 26px;
    height: 30px;
    border: none;
    cursor: pointer;
    border-radius: 3px;
    background-color: hsl(234, 92%, 63%);
    color: white;
    transition: all 0.3s;
}

.auth-form > button:hover {
    background-color: hsl(234, 87%, 73%);
}

a {
    text-decoration: none;
    background-color: hsl(0, 0%, 72%);
    color: white;
    padding: 5px;
    border-radius: 3px;
    margin-left: 10px;
    transition: all 0.3s;
}
a:hover {
    background-color: hsl(0, 0%, 50%);
}
.all-auth {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.login-google-div {
    display: flex;
    justify-content: center;
    width: 300px;
    margin-bottom: 18px;
}

.login-google-button {
    background-color: white;
    border: 1px rgb(189, 189, 189) solid;
    border-radius: 3px;
    width: 100%;
    cursor: pointer;
}
.login-google {
    width: 30px;
    height: 30px;
}